
// Setup
// ============
// General variables set here

@import "setup/_setup";


// Library
// ============
// Static framework variables and mixins

/* csslint ignore:start */
@import "library/_normalize";
@import "library/_sprites";
@import "library/_framework-typography";
@import "library/mixins/_flexbox";
@import "library/mixins/_grid";
@import "library/mixins/_clearfix";
@import "library/mixins/_iconfix";
/* csslint ignore:end */


// Fontello
// ============
// Pulls in fontello CSS

@import "fontello/fontello";


// Global
// ============
// Common SASS shared between components

@import "global/_vars.scss";
@import "global/_patterns.scss";
@import "global/_layout.scss";
@import "global/_typography.scss";


// Components
// ============
// User components, these are auto injected by Gulp when saved to folder

/* inject:imports */
@import "components/_footer.scss";
@import "components/_masthead.scss";
@import "components/_welcome.scss";
/* endinject */
